/* ===============================
   GLOBAL RESET
================================ */
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Segoe UI", Roboto, Arial, sans-serif;
  background-color: #f5f7fb;
  color: #1f2937;
  line-height: 1.6;
}

/* ===============================
   NAVBAR
================================ */
.navbar {
  background: #0b1220;
  position: sticky;
  top: 0;
  z-index: 1000;
}

.nav-container {
  max-width: 1200px;
  margin: auto;
  padding: 14px 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.brand {
  display: flex;
  align-items: center;
  gap: 14px;
  color: #ffffff;
  font-size: 20px;
  font-weight: 600;
}

.logo {
  height: 200px; /* untouched as requested */
  width: auto;
}

/* NAV LINKS */
nav a {
  color: #d1d5db;
  text-decoration: none;
  margin-left: 24px;
  font-size: 15px;
  font-weight: 500;
  transition: color 0.3s ease;
}

nav a:hover {
  color: #38bdf8;
}

/* ===============================
   HERO SECTION
================================ */
.hero {
  background: linear-gradient(135deg, #0b1220, #1e293b);
  color: #ffffff;
  padding: 90px 20px;
  text-align: center;
}

.hero h1 {
  font-size: 2.8rem;
  margin-bottom: 14px;
  font-weight: 700;
}

.hero p {
  font-size: 1.15rem;
  max-width: 720px;
  margin: auto;
  color: #cbd5f5;
}

/* ===============================
   WHY SECTION
================================ */
.why {
  max-width: 1200px;
  margin: 70px auto;
  padding: 0 24px;
  text-align: center;
}

.why h2 {
  font-size: 2.3rem;
  margin-bottom: 36px;
  font-weight: 700;
}

.why-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 26px;
}

.card {
  background: #ffffff;
  padding: 26px;
  border-radius: 12px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.06);
  font-weight: 500;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.card:hover {
  transform: translateY(-6px);
  box-shadow: 0 16px 40px rgba(0, 0, 0, 0.1);
}

/* ===============================
   COMMON PAGE LAYOUT
================================ */
.page {
  max-width: 1100px;
  margin: 70px auto;
  padding: 0 24px;
}

.page h2 {
  font-size: 2.4rem;
  text-align: center;
  margin-bottom: 22px;
  font-weight: 700;
  color: #0b1220;
}

.page p {
  max-width: 900px;
  margin: 0 auto 18px;
  text-align: center;
  font-size: 1.05rem;
  color: #4b5563;
}

/* ===============================
   SERVICES PAGE
================================ */
.services {
  list-style: none;
  padding: 0;
  margin-top: 42px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 26px;
}

.services li {
  background: #ffffff;
  padding: 30px;
  border-radius: 14px;
  box-shadow: 0 10px 28px rgba(0, 0, 0, 0.08);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.services li:hover {
  transform: translateY(-6px);
  box-shadow: 0 16px 38px rgba(0, 0, 0, 0.12);
}

.services li strong {
  display: block;
  font-size: 1.15rem;
  margin-bottom: 10px;
  color: #0b1220;
}

/* ===============================
   CONTACT PAGE
================================ */
.contact-text {
  max-width: 760px;
  margin: 0 auto 40px;
  font-size: 1.05rem;
  color: #4b5563;
}

.contact-box {
  background: #ffffff;
  max-width: 520px;
  margin: auto;
  padding: 32px;
  border-radius: 14px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
}

.contact-box p {
  font-size: 1.05rem;
  margin-bottom: 18px;
  color: #1f2937;
}

.contact-box a {
  margin-left: 6px;
  color: #2563eb;
  font-weight: 600;
  text-decoration: none;
}

.contact-box a:hover {
  text-decoration: underline;
}

/* ===============================
   FOOTER
================================ */
.footer {
  background: #0b1220;
  color: #9ca3af;
  text-align: center;
  padding: 22px;
  margin-top: 80px;
  font-size: 14px;
}

/* ===============================
   RESPONSIVE
================================ */
@media (max-width: 768px) {
  .nav-container {
    flex-direction: column;
    gap: 14px;
  }

  nav a {
    margin: 0 10px;
  }

  .hero h1 {
    font-size: 2.1rem;
  }

  .page h2 {
    font-size: 2rem;
  }
}
